import React from "react"
import { Navigate, useRoutes } from "react-router-dom"
import Login from "@/pages/Login"
import lazyLoad from "@/routers/utils/lazyLoad"

import Error from './modules/error'

// * 
const routerArray = [
	{
		path: "/demo",
		element: lazyLoad(React.lazy(() => import("@/pages/Demo"))),
		meta: {
			requiresAuth: true,
			title: "3D",
			key: "demo"
		}
	},
	{
		path: "/demo/NarutoKick",
		element: lazyLoad(React.lazy(() => import("@/pages/Demo/components/NarutoKick"))),
		meta: {
			requiresAuth: true,
			key: "NarutoKick",
		}
	},
]
routerArray.push(...Error)

export const rootRouter = [
	{
		path: "/",
		element: <Navigate to="/login" />
	},
	{
		path: "/login",
		element: <Login />,
		meta: {
			requiresAuth: false,
			title: "",
			key: "login"
		}
	},
	{
		path: "/dataScreen",
		element: lazyLoad(React.lazy(() => import("@/pages/DataScreen"))),
		meta: {
			requiresAuth: true,
			title: "",
			key: "dataScreen"
		}
	},
	...routerArray,
	{
		path: "*",
		element: <Navigate to="/404" />
	}
]

const Router = () => {
	const routes = useRoutes(rootRouter)
	return routes
}

export default Router
